.slide {
    background-color: white;
    width: 100vw;
    min-height: 100vh;
    position: absolute;
    left: 0;
    top: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background-size: cover;
    background-position: center;
    color: white;
    padding: 20px;

    .slide-title, .slide-content {
        text-shadow: 2px 0px 0px black,
                    -2px 0px 0px black,
                    0px 2px 0px black,
                    0px -2px 0px black;
        text-align: center;
    }

    .slide-title {
        font-size: 3rem;
    }

    .slide-content {
        font-size: 5rem;
    }

    .clickarea {
        height: 100vh;
        width: 50vw;
        position: absolute;
        top: 0;
    }

    .left {
        left: 0;
    }

    .right {
        right: 0;
    }
}



